.App{
  text-align: center;
}
div{
  padding: 10px;
}
select{
  padding: 5px;
}
body{
  background: linear-gradient(135deg, orange 30%, cyan) no-repeat;
}
select{
  border-radius: 15px;
  text-align: center;
  background-color: rgb(217, 241, 128);
  
}
option{
  font-weight: 300;
}
button{
  border-radius: 13px;
  text-align: center;
  font-size: 15px;
  font-weight: 200;
  background-color: rgb(217, 241, 128);
}
textarea{
  border-radius: 13px;
  text-align: center;
  font-size: 15px;
  font-weight: 200;
  background-color: rgb(93, 232, 241);
}
